.hero {
	background-color: $blue;
	padding: 0 0 50px;
	@include sm {
		text-align: center; }
	&__wrap {
		display: flex;
		align-items: flex-start;
		justify-content: space-between; }
	&__col {
		flex: 1 1 50%; }
	&__title {
		margin: 90px 0 35px;
		max-width: 380px;
		@include sm {
			font-size: 28px;
			margin: 15px 0;
			max-width: 100%; } }
	&__description {
		max-width: 510px;
		padding-right: 20px;
		font-size: 15px;
		font-weight: 400;
		line-height: 2;
		color: $white-blue;
		@include sm {
			max-width: 100%;
			line-height: 1.4;
			padding-right: 0; } }
	&__btn {
		margin: 30px 0 14px;
		@include sm {
			margin: 15px 0; }
		.btn {
			text-align: center; } }

	&__link-video {
		display: flex;
		align-items: center;
		margin-left: 40px;
		opacity: .8;
		@include sm {
			justify-content: center;
			margin-left: 0; } }
	&__link-icon {
		margin-top: 3px;
		margin-left: 5px;
		width: 17px;
		height: auto; }
	&__link {
		font-size: 15px;
		line-height: 1.4;
		font-weight: 400;
		color: $white-blue;
		border-bottom: 1px solid $white-blue;
		transition: border-bottom .3s;
		&:hover {
			border-bottom: 1px solid transparent; } }
	&__image {
		flex: 1 1 50%;
		img {
			width: 100%; }
		@include sm {
			display: none; } }
	&__brends {
		display: flex;
		align-items: center;
		justify-content: flex-end;
		padding-top: 45px;
		padding-right: 75px;
		@include sm {
			justify-content: center;
			flex-wrap: wrap;
			padding-right: 0;
			padding-top: 22px; } } }

.info {
	margin-right: 30px;
	font-size: 15px;
	font-weight: 400;
	line-height: 20px;
	color: $white-blue;
	@include sm {
		margin-bottom: 10px; } }

.brend {
	&__list {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		@include sm {
			justify-content: center; } }
	&__item {
		margin-top: 10px;
		margin-right: 30px;
		@include sm {
			margin-right: 15px; } } }
