.features {
	padding-bottom: 120px;
	@include sm {
		padding-bottom: 30px; }
	&__title {
		max-width: 570px;
		margin: 0 auto 56px;
		text-align: center;
		@include sm {
			margin: 0 auto 28px; } }
	&__content {
		padding: 0 26px;
		@include sm {
			padding: 0; } }
	&__row {
		display: flex;
		@include sm {
			flex-direction: column; }
		&:not(:last-child) {
			margin-bottom: 67px;
			@include sm {
				margin-bottom: 20px; } } }
	&__img {
		width: 57.3%;
		@include clr;
		@include sm {
			order: -1;
			width: 100%; }
		&:first-child {
			padding-right: 10px;
			@include sm {
				padding-right: 0; }
			img {
				max-width: 500px;
				@include sm {
					max-width: 100%; } } }
		&:last-child {
			padding-left: 10px;
			@include sm {
				padding-left: 0; }
			img {
				float: right;
				max-width: 577px; } }
		img {
			width: 100%;
			display: block; } }
	&__text {
		width: 42.7%;
		@include sm {
			width: 100%;
			padding-top: 23px; }
		&:first-child {
			padding-top: 23px; } }
	&__subtitle {
		font-size: 25px;
		line-height: 30px;
		margin-bottom: 35px;
		color: $gray-blue;
		font-weight: 400;
		@include sm {
			font-size: 18px;
			line-height: 28px;
			margin-bottom: 17px; } }
	&__description {
		font-size: 23px;
		line-height: 32px;
		color: $light-gray;
		letter-spacing: -0.04em;
		font-weight: 400;
		@include sm {
			font-size: 14px;
			line-height: 25px; }
		p:not(:last-child) {
			margin-bottom: 40px;
			@include sm {
				margin-bottom: 15px; } } } }



