.slider {
	border-bottom: 1px solid $darken-blue;
	border-top: 1px solid $darken-blue;
	margin-bottom: 65px;

	@include sm {
		margin-bottom: 30px; }
	.slick-list {
		border-left: 1px solid $darken-blue;
		border-right: 1px solid $darken-blue; }
	&__prev {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: -38px;
		cursor: pointer;
		background: transparent;
		@include sm {
			left: -20px; }
		.icon-arrow {
			fill: $blue;
			width: 28px;
			height: 31px;
			@include sm {
				width: 12px;
				height: 15px; } } }
	&__next {
		position: absolute;
		top: 50%;
		transform: translateY(-50%) rotate(-180deg);
		right: -38px;
		cursor: pointer;
		background: transparent;
		@include sm {
			right: -20px; }
		.icon-arrow {
			fill: $blue;
			width: 28px;
			height: 31px;
			@include sm {
				width: 12px;
				height: 15px; } } }
	&__inner {
		padding: 0 45px;
		max-width: 1260px;
		@include sm {
			padding: 0 25px; } } }

.product {
	padding: 26px 0 30px;
	text-align: center;
	margin-left: -1px;
	border-left: 1px solid $darken-blue;
	@include sm {
		padding: 13px 0 15px; }
	&__title {
		margin-bottom: 23px;
		display: block;
		font-size: 20px;
		line-height: 27px;
		font-weight: 800;
		color: $night-blue;
		text-transform: uppercase;
		@include sm {
			font-size: 12px;
			line-height: 20px; } }
	&__img {
		width: 88px;
		margin: 0 auto;
		display: block;
		height: 200px;
		img {
			display: block;
			width: 100%; } } }
